.App{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.calculator {
  position: relative;
  width: 340px;
  padding: 20px;
  border-radius: 20px;
  background: #33393e;
  box-shadow: 15px 15px 20px rgba(0, 0, 0, 0.25),
      -15px -15px 20px rgba(255, 255, 255, 0.1);
}

.calculator .buttons {
  position: relative;
  display: grid;
}

.calculator .buttons .value {
  position: relative;
  grid-column: span 4;
  user-select: none;
  overflow: none;
  width: 90%;
  text-align: end;
  height: 100px;
  border-radius: 10px;
  margin-bottom: 12px;
  padding: 0 20px;
  font-size: 20px;
  font-weight: 500;
  background: #33393e;
  color: #eee;
  box-shadow: inset 15px 15px 20px rgba(0, 0, 0, 0.5),
      inset -15px -15px 20px rgba(255, 255, 255, 0.1);
}

.calculator .buttons .btn {
  position: relative;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  cursor: pointer;
  user-select: none;
  min-width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2em;
  color: #eee;
  border: 2px solid #333;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.25),
      -2px -2px 5px rgba(255, 255, 255, 0.1);
}

.clear{
  grid-column: span 2;
  background-color: #f44336;
  color: #fff;
  border: 2px solid #edf1f4;
}

.plus{
  grid-row: span 2;
  background-color: #31a935;
  color: #fff;
  border: 2px solid #edf1f4;
}

.equal {
  grid-row: span 2;
  background-color: #2196f3;
  color: #fff;
  border: 2px solid #edf1f4;
}
.developer{
  grid-column: span 4;
  font-size: 10px !important;
}
